import React,{Component} from 'react'
import { Layout } from 'antd';
const { Sider } = Layout;
import Header from "./header.jsx";
import MenuBar from "./menuBar.jsx";

export class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <Layout>
                <Header />
                <Layout>
                    <Sider width={200} style={{ background: '#fff' }}>
                        <MenuBar />
                    </Sider>
                    {this.props.children}
                </Layout>
            </Layout>
        )
    }
}
module.exports = App;


