import React,{Component} from 'react'
import { Row, Col, Button, Form, Input, message } from 'antd';
const FormItem = Form.Item;

export class ChangePwdForm extends React.Component {
    constructor(props) {
        super(props);
        ['handleSubmit', 'checkPassword'].forEach(m=>this[m] = this[m].bind(this))
    }

    checkPassword(rule, value, callback) {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('newPassword')) {
            callback('两次密码输入不一致!');
        } else {
            callback();
        }
    }

    handleSubmit() {
        let that = this;
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                ajax({
                    url: "/changePwd",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("修改密码成功!", 1);
                            hashHistory.push("/common/login");
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    render () {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 }
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 14 }
            }
        };
        return (
            <Form onSubmit={this.handleSubmit}>
                <FormItem
                    {...formItemLayout}
                    label="旧密码"
                    hasFeedback
                >
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: '请输入旧密码'
                        }]
                    })(
                        <Input type="password" placeholder="请输入旧密码" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="新密码"
                    hasFeedback
                >
                    {getFieldDecorator('newPassword', {
                        rules: [{
                            required: true, message: '请输入新密码'
                        }]
                    })(
                        <Input type="password" placeholder="请输入新密码" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="确认密码"
                    hasFeedback
                >
                    {getFieldDecorator('confirmPassword', {
                        rules: [{
                            required: true, message: '请再次输入密码'
                        },{
                            validator: this.checkPassword
                        }]
                    })(
                        <Input type="password" placeholder="请确认新密码" />
                    )}
                </FormItem>
            </Form>
        );
    }
}

const WrappedChangePwdForm = Form.create()(ChangePwdForm);

module.exports = WrappedChangePwdForm;


