import React,{Component} from 'react'
import {hashHistory} from 'react-router'
import { Layout, Button, Modal, Popconfirm } from 'antd';
import ChangePwdForm from '../../common/components/changePwdForm.jsx'
const { Header } = Layout;

export class AdminHeader extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false
        };
        ['showModal', 'handleSave', 'hideModal', 'logout'].forEach(m=>this[m] = this[m].bind(this))
    }

    showModal() {
        this.setState({
            visible: true
        });
    }

    hideModal() {
        this.setState({
            visible: false
        });
    }
    handleSave() {
        this.formRef.handleSubmit();
    }

    logout() {
        ajax({
            url: "/logout",
            method: "post",
            data: {},
            callback: function(data) {
                if(data.status == 200) {
                    hashHistory.push("/common/login");
                } else {
                    message.error(data.errMsg, 1)
                }
            }
        });
    }

    render() {
        return (
            <Header className="header">
                <div className="logo">阳光融资管理后台</div>
                <div className="info">
                    <div className="name">您好! {localStorage.getItem("name")}</div>
                    <div className="btns">
                        <Button type="primary" style={{"marginRight":"10px"}} onClick={this.showModal}>修改密码</Button>
                        <Modal
                            title="修改密码"
                            visible={this.state.visible}
                            onOk={this.handleSave}
                            onCancel={this.hideModal}
                        >
                            <ChangePwdForm wrappedComponentRef={(inst) => this.formRef = inst} closeModal={this.hideModal} />
                        </Modal>
                        <Popconfirm placement="bottom" title="是否确认退出?" onConfirm={this.logout} okText="Yes" cancelText="No">
                            <Button>退出</Button>
                        </Popconfirm>
                    </div>
                </div>
            </Header>
        )
    }
}

module.exports = AdminHeader;


